<template>
  <div class="course-vedio">
    <head-top :title="title" />
    <PlayBox
      ref="PlayBox"
      :Videos.sync="videoSrc"
      :SelectAble="false"
      :autoFull="false"
      :name="title"
    />
    <van-tabs
      v-model="tabSelected"
      sticky
      color="rgba(0, 203, 171, 1)"
      :ellipsis="false"
      :line-height="2"
      title-active-color="#00CBAB"
    >
      <van-tab title="简介"></van-tab>
      <van-tab :title="tabTitle" :disabled="!(Rdata && Rdata.ECAllowComment === 1)"></van-tab>
    </van-tabs>
    <div class="Introduction" v-if="tabSelected == 0">
      <div class="first">
        <p>{{ intro.Teachers }}：{{ intro.ECTitle }}</p>
        <p>
          <van-icon name="eye-o" />
          <span>{{ intro.Visits }}次播放&nbsp; | &nbsp; {{ intro.Discipline }}</span>
        </p>
        <p>
          <span :class="{ info: isactive }">{{ intro.Note }}</span>
          <span style="color:#00CBAB;float:right" @click="expandA">{{ expandText }}</span>
        </p>
        <p id="slider-in"></p>
        <van-pagination
          v-model="currentPage"
          :page-count="pageCount"
          :show-page-size="pageCount"
          @change="ChangePlay"
        />
      </div>
      <div class="second"></div>
      <div class="third">
        <div class="title">相关推荐</div>
        <div class="img">
          <div v-for="(s, i) in RelevantList" :key="i" @click="ChangePlay(s)">
            <van-image
              :src="s.imgUrl"
              alt=""
              width="100%"
              class="course-img"
              @click="ToDetail(s.id)"
              ><img :src="defaultBG" slot="error" style="width:100%"
            /></van-image>
            <p>{{ s.title }}</p>
          </div>
        </div>
      </div>
    </div>
    <div class="comment" v-if="tabSelected == 1">
      <div class="title">全部评论</div>
      <div class="content" v-for="(s, i) in comment" :key="i">
        <div>
          <div name="top">
            <van-image round width="1rem" height="1rem" :src="s.advatar">
              <img :src="defaultUrl" alt="" slot="error" />
            </van-image>
            <div style="marginLeft:0.2rem;color:#999">
              <p>{{ s.UserName }}</p>
              <p>{{ s.ECCTime | formatDateTime }}</p>
            </div>
          </div>
          <div class="text isshow" v-if="index != i" v-html="s.ECCContent"></div>
          <div class="text" v-if="index == i" v-html="s.ECCContent"></div>
          <div class="expand" @click="expandB(i)" v-if="index != i && s.ex">
            全文
          </div>
          <div class="expand" @click="expandB(-1)" v-if="index == i && s.ex">
            收起
          </div>
          <div style="textAlign:left;color:#999;marginTop:12px">
            <van-icon name="chat-o" @click="changeCommenter(s.ECCContent, s.UserName)"
              >回复</van-icon
            >
            <van-icon name="delete" @click="DeleteCommenter(s.ECCID)" v-if="s.UserID == uid"
              >删除</van-icon
            >
          </div>
        </div>
      </div>
    </div>
    <div class="bottom-video">
      <textarea
        type="text"
        v-model="commentText"
        ref="commentArea"
        :disabled="!(Rdata && Rdata.ECAllowComment === 1)"
      />
      <button @click="addComment" :disabled="!(Rdata && Rdata.ECAllowComment === 1)">
        发送
      </button>

      <div>
        <p class="iconfont icon-fenxiang1"></p>
        <p>分享</p>
      </div>
      <div v-if="!IsFav" @click="AddFav">
        <p class="iconfont icon-shoucang1"></p>
        <p>收藏</p>
      </div>
      <div v-else @click="RemoveFav">
        <p class="iconfont icon-shoucang1" style="color:#FFAD32"></p>
        <p>已收藏</p>
      </div>
      <!-- <div>
        <p class="iconfont icon-huancun1"></p>
        <p>缓存</p>
      </div> -->
    </div>
  </div>
</template>

<script>
import { utf16toEntities, entitiestoUtf16 } from "@/utils/commom"
export default {
  components: {
    PlayBox: () => import("../../../components/Player/PlayBox")
  },
  data() {
    return {
      currentPage: 1,
      IsFav: false,
      videoSrc: "",
      mCount: 105, // 评论条数
      isactive: true,
      isactive2: true,
      expandText: "详情",
      tabOptions: [
        { title: "简介", value: 0 },
        { title: "评论", value: 1 }
      ],
      tabSelected: 0,
      commentText: "",
      index: -1,
      intro: {},
      comment: [],
      title: "",
      pageCount: 1,
      RelevantList: [],
      defaultBG: require("@/assets/resources.png"),
      defaultUrl: require("@/assets/default_head.png"),
      pageSize: 100,
      page: 0,
      ifSend: false,
      Rdata: "",
      tabIndex: 0,
      tabTitle: "",
      Pid: "",
      lastText: "",
      repley: false,
      uid: ""
    }
  },
  mounted() {
    this.pid = this.$route.params.id
    this.LoadData(this.pid)
    this.LoadComments()
    this.uid = this.$store.getters.userId
  },
  methods: {
    ChangePlay() {
      const path = this.intro.pathArry[this.currentPage - 1]
      this.$refs.PlayBox.Pause()
      this.videoSrc = `http://${this.$root.SrvConfigInfo.VODHost}:${this.$root.SrvConfigInfo.VODPort}/vod/${path}`
    },
    ToDetail(id) {
      this.pid = id
      this.LoadData(id)
      this.LoadComments()
    },
    expandA() {
      this.isactive = !this.isactive
      if (this.isactive) this.expandText = "详情"
      else this.expandText = "概略"
    },
    expandB(i) {
      this.index = i
    },
    async LoadData(eid) {
      const res = await this.$mainHttp.ElaborateCourses.GetData({
        ecid: eid,
        updateVisits: true
      })

      if (res && res.Data) {
        this.Rdata = res.Data
        this.IsFav = res.IsFav
        if (res.Data.ECInfo) {
          let ec = JSON.parse(res.Data.ECInfo)
          for (var o in ec) {
            res.Data[o] = ec[o]
          }
        }
        delete res.Data.ECInfo
        for (var s in res.Data) {
          this.intro[s] = res.Data[s]
        }
        this.LoadOther(eid, this.intro.Teachers)
        this.title = this.intro.ECTitle
        this.pageCount = res.Materials.length
        this.intro.pathArry = []
        res.Materials.forEach((element, index) => {
          this.intro.pathArry.push(element.MaterialStorePath)
          if (index === 0) {
            this.videoSrc = `http://${this.$root.SrvConfigInfo.VODHost}:${this.$root.SrvConfigInfo.VODPort}/vod/${element.MaterialStorePath}`
          }
        })
      }
    },
    async LoadOther(id, k) {
      this.RelevantList = []
      const res1 = await this.$mainHttp.ElaborateCourses.GetRelevantDatas({
        ecid: id,
        key: k,
        type: 0
      })
      if (res1 && res1.Datas) {
        let url = this.$mainHttp.common.downloadURL()
        res1.Datas.forEach((element, index) => {
          if (index < 10) {
            this.$set(element.Info, "ECInfo", JSON.parse(element.Info.ECInfo))
            this.RelevantList.push({
              id: element.Info.ECID,
              imgUrl: `${url}?filename=${element.MaterialID}&fileType=9`,
              title: element.Info.ECTitle
            })
          }
        })
      }
    },
    async LoadComments() {
      this.comment = []
      const res = await this.$mainHttp.ElaborateCourses.GetComments({
        ecid: this.pid,
        psize: this.pageSize,
        page: this.page
      })
      if (res.Datas) {
        let url = this.$mainHttp.common.downloadURL()
        res.Datas.forEach(element => {
          element.advatar = `${url}?filename=${
            element.UserID
          }&fileType=1&platFormType=1&${Math.random()}`
          element.ECCContent = element.ECCContent.replace(RegExp("<br/><br/>", "gm"), "<br/>")
          element.ECCContent = entitiestoUtf16(element.ECCContent)
          let str = element.ECCContent.match(/<br\/>/g)
          if (str && str.length > 5) {
            element.ex = true
          }
        })
        this.comment = res.Datas
        this.tabTitle = "评论" + "(" + res.Datas.length + ")"
      } else {
        this.tabTitle = "评论" + "(" + 0 + ")"
      }
    },
    async DeleteCommenter(id) {
      const res = await this.$mainHttp.ElaborateCourses.DelComment({
        eccid: id
      })
      if (res) {
        this.LoadComments()
      }
    },
    changeCommenter(content, name) {
      this.lastText = `回复@${name}：${content}`
      this.repley = true
      content = content.replace(/<span[^>]+>/g, "[符号表情]").replace(/<\/span>/g, "")
      content = content.replace(RegExp("<br/>", "gm"), "\n")
      this.commentText = `回复@${name}：${content} \n----------\n`
      this.$refs["commentArea"].focus()
    },
    async addComment() {
      let comment = this.commentText
      if (this.repley) {
        var index = this.commentText.lastIndexOf("----------")
        let hou = this.commentText.slice(index)
        comment = this.lastText + "<br/>" + hou
      }
      comment = utf16toEntities(comment)
      if (this.commentText) {
        const res = await this.$mainHttp.ElaborateCourses.AddComment({
          ecid: this.pid,
          content: comment
        })
        if (res) {
          this.LoadComments()
        }
      }
      this.repley = false
      this.commentText = ""
    },
    async AddFav() {
      const res = await this.$mainHttp.ElaborateCourses.AddFav({
        ecid: this.pid
      })
      if (res) {
        this.IsFav = !this.IsFav
      }
    },
    async RemoveFav() {
      const res = await this.$mainHttp.ElaborateCourses.DelFav({
        ecfid: this.pid
      })
      if (res) {
        this.IsFav = !this.IsFav
      }
    }
  }
}
</script>

<style lang="less">
@import "../../../styles/emoji.less";
.emoji-item-common {
  background: url("../../../assets/emoji_sprite.png");
  display: inline-block;
  &:hover {
    cursor: pointer;
  }
}
.emoji-size-small {
  // 表情大小
  zoom: 0.3;
}
.emoji-size-large {
  zoom: 0.4; // emojipanel表情大小
  margin: 4px;
}
.course-vedio {
  height: 100%;
  overflow-y: auto;
  #headTop .header_title {
    font-size: 16px;
  }
  #course-vedio-img {
    height: 210px;
  }
  .van-tab__text {
    font-size: 16px;
  }
  .title {
    text-align: left;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 12px;
  }
  .Introduction {
    .van-pagination {
      width: 100%;
      overflow-x: auto;
    }

    .van-pagination::-webkit-scrollbar {
      width: 16px;
      height: 1px;
      background-color: #fff;
    }
    .first {
      padding: 15px;
      background-color: #fff;
      > p {
        text-align: left;
        margin-bottom: 6px;
      }
      > p:first-child {
        font-size: 16px;
      }
      > p:nth-child(2) {
        font-size: 12px;
      }
      > p:nth-child(3) {
        font-size: 14px;
        margin-bottom: 30px;
      }
      .info {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }
      #slider-in {
        border-bottom: 1px solid rgba(221, 221, 221, 1);
      }
      .van-pagination__prev,
      .van-pagination__next {
        display: none;
      }
      .van-pagination__item {
        min-width: 40px;
        margin-left: 10px;
        border: 1px solid #ddd;
        color: #666;
        margin-top: 12px;
      }
      .van-pagination__item--active {
        color: rgb(0, 203, 171);
        background-color: #fff;
        border: 1px solid rgb(0, 203, 171);
      }
    }

    .img {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      img {
        width: calc(50vw - 25px);
        height: calc(30vw - 15px);
        border-radius: 5px;
      }
      > div:nth-child(2n + 1) {
        margin-left: 0px;
      }
      > div {
        margin-left: 20px;
        margin-bottom: 16px;
      }
      p {
        text-align: left;
        width: calc(50vw - 25px);
      }
    }
    .third {
      padding: 15px;
      margin-top: 16px;
      margin-bottom: 40px;
      background-color: #fff;
    }
  }
  .comment {
    background-color: #fff;
    margin-bottom: 40px;
    padding: 15px;
    .content > div {
      margin-left: 30px;
      margin-bottom: 16px;
    }
    * {
      font-size: 14px;
    }
    .title {
      font-size: 18px;
      margin-left: 0px;
    }
    div[name="top"] {
      display: flex;
      text-align: left;
      margin-left: -30px;
      margin-bottom: 10px;
    }
    .text {
      text-align: left;
    }
    .isshow {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 6;
      overflow: hidden;
      line-height: 20px;
      margin-top: -5px;
    }
    .van-icon::before {
      display: inline-block;
      margin-right: 4px;
    }
    .van-icon-delete {
      position: relative;
      left: 16px;
      top: 2px;
    }
    .expand {
      text-align: left;
      color: #00cbab;
      margin-top: 5px;
      margin-bottom: 15px;
    }
    .van-image--round img {
      width: 1rem;
      height: 1rem;
    }
  }
  .bottom-video {
    position: fixed;
    bottom: -10px;
    z-index: 10000;
    border-top: 1px solid #e1e5e4;
    height: 70px;
    width: 100%;
    display: flex;
    background-color: #fff;
    justify-content: space-around;
    ::-webkit-input-placeholder {
      color: #999;
      padding-left: 5px;
    }
    textarea {
      background-color: #f7f7f7;
      height: 45px;
      margin-top: 10px;
      width: 200px;
      padding: 0px 5px;
      border: none;
    }
    div > p:last-child {
      font-size: 10px;
      color: #999999;
    }
    div > p {
      color: #999999;
    }
    div {
      margin-top: 15px;
    }
    button {
      height: 32px;
      padding-left: 12px;
      padding-right: 12px;
      background: #00cbab;
      color: #fff;
      position: relative;
      top: 16px;
    }
  }
}
</style>



